@import "https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  background-color: #f7f9fd;
}

.navbar {
  text-align: center;
  background-color: #4caf50;
  padding: 10px 0;
}

.navbar ul {
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navbar ul li {
  margin: 0 15px;
}

.navbar ul li a {
  color: #fff;
  padding: 10px 15px;
  font-size: 18px;
  text-decoration: none;
  transition: all .3s;
}

.navbar ul li a:hover {
  background-color: #367c39;
  border-radius: 5px;
}

.wrapper {
  width: 90%;
  max-width: 43.753m;
  margin: 1em auto;
  font-size: 16px;
}

.container {
  width: 100%;
}

.sub-container {
  grid-template-columns: 1fr 1fr;
  gap: 3em;
  width: 100%;
  display: grid;
}

.flex {
  align-items: center;
  display: flex;
}

.flex-space {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.wrapper h3 {
  color: #363d55;
  margin-bottom: .6em;
  font-weight: 500;
}

.container input, select {
  color: #414a67;
  border: 1px solid #d0d0d0;
  border-radius: .3em;
  outline: none;
  width: 100%;
  margin-bottom: .6em;
  padding: .6em .3em;
  font-weight: 400;
  display: block;
}

.container input:focus {
  border-color: #4caf50;
}

.total-amount-container, .user-amount-container {
  background: #fff;
  border-radius: .3em;
  padding: 1.25em .9em;
  box-shadow: 0 .6em 1.2em #1c00500f;
}

.output-container {
  color: #fff;
  background-color: #4caf50;
  border-radius: .3em;
  margin: 2em 0;
  padding: 1.2em;
  box-shadow: 0 .6em 1.2em #1c00500f;
}

.output-container p {
  margin-bottom: .6em;
  font-weight: 500;
}

.output-container span {
  text-align: center;
  color: #e5e5e5;
  font-weight: 400;
  display: block;
}

.submit {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: .3em;
  outline: none;
  margin-top: .8em;
  padding: .6em 1.2em;
  font-size: 1em;
}

.list {
  background-color: #fff;
  border-radius: .6em;
  padding: 1.8em 1.2em;
  box-shadow: 0 .6em 1.2em #1c00500f;
}

.sublist-content {
  border-left: .3em solid #4caf50;
  justify-content: space-between;
  width: 100%;
  margin-bottom: .6em;
  padding: .5em 1em;
  display: flex;
}

.sublist-content p, button {
  margin: 0 auto;
}

.list:first-child {
  width: 20%;
}

.product {
  color: #363d55;
  font-weight: 500;
}

.amount {
  color: #414a67;
  margin-left: 20%;
}

.icons-container {
  align-items: center;
  width: 5em;
  margin: 1.2em;
}

.product-title {
  margin-bottom: 1em;
}

.edit {
  margin-left: auto;
}

.edit, .delete {
  cursor: pointer;
  color: #4caf50;
  background: none;
  border: none;
  margin-right: 1.5em;
}

@media screen and (width <= 600px) {
  .wrapper {
    font-size: 14px;
  }

  .sub-container {
    grid-template-columns: 1fr;
    gap: 1em;
  }
}
